import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import RowBank from "./row";
import { useDispatch, useSelector } from "react-redux";
import CreateBank from "./create";
import find from "../../../../services/utilities/axioKit/find";
import { BROWSE } from "../../../../services/redux/slices/banks";
import Pagination from "../../../../components/pagination";
import { handlePagination } from "../../../../services/utilities";
import TableRowCount from "../../../../components/pagination/rows";

export default function Banks() {
  const { token } = useSelector(({ persons }) => persons);
  const { selectedExam } = useSelector(({ banks }) => banks);
  const [modal, setModal] = useState(false);

  const dispatch = useDispatch();
  const [banks, setBanks] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedRowCount, setSelectedRowCount] = useState(5);

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }
  }, [dispatch, token]);

  useEffect(() => {
    if (selectedExam?._id) {
      const handleBanks = async () => {
        const banks = await find(
          "banks",
          "find",
          "exam",
          selectedExam._id,
          token
        );

        setBanks(banks);
        localStorage.setItem("selectedExam", JSON.stringify(selectedExam));
      };

      handleBanks();
      return () => {
        localStorage.removeItem("selectedExam");
      };
    }
  }, [dispatch, selectedExam, token]);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const { currentItems, totalPages, indexOfFirstItem } = handlePagination(
    banks,
    currentPage,
    selectedRowCount
  );

  const handleChangeRowCount = (value) => {
    setCurrentPage(1);
    setSelectedRowCount(value);
  };

  const toggle = () => {
    setModal(!modal);
  };

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 mx-4 mb-3 d-flex justify-content-between align-items-center"
        >
          <div></div>
          <a href="#!" className="white-text mx-3">
            {selectedExam.title} Banks ({banks.length} Items)
          </a>
          <div className="text-right">
            <MDBBtn
              color="white"
              outline
              rounded
              size="sm"
              className="px-2"
              onClick={toggle}
            >
              <MDBIcon icon="plus" className="mt-0" />
            </MDBBtn>

            <CreateBank modal={modal} toggle={toggle} />
          </div>
        </MDBView>
        <MDBCardBody>
          {banks.length === 0 ? (
            <div className="text-center">Banks Not Found..</div>
          ) : (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th>#</th>
                  <th className="th-lg">Question</th>
                  <th className="th-lg">Choices</th>
                  <th className="th-lg">Answer</th>
                  <th className="th-lg">Action</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {currentItems.map((bank, index) => (
                  <RowBank
                    key={index + indexOfFirstItem}
                    bank={bank}
                    toggle={toggle}
                    index={index + indexOfFirstItem}
                  />
                ))}
              </MDBTableBody>
            </MDBTable>
          )}
          {banks.length > 5 && (
            <div className="ml-3">
              <TableRowCount
                handleChange={handleChangeRowCount}
                selectedRowCount={selectedRowCount}
              />
            </div>
          )}

          {banks.length > selectedRowCount && (
            <Pagination
              totalPages={totalPages}
              currentPage={currentPage}
              onPageChange={handlePageChange}
            />
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
