import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTabContent,
  MDBTabPane,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import React, { useState, useEffect } from "react";
import RowPerson from "./row";
import { useDispatch, useSelector } from "react-redux";
import { BROWSE } from "../../../../services/redux/slices/persons";
import classNames from "classnames";
import Pagination from "../../../../components/pagination";
import { handlePagination } from "../../../../services/utilities";
import TableRowCount from "../../../../components/pagination/rows";

const tabItems = [
  { value: "Administrator", icon: "user-lock" },
  { value: "Examinee", icon: "user-edit" },
  { value: "Receptionist", icon: "user-clock" },
];

export default function Persons() {
  const { persons, token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const [activeItem, setActiveItem] = useState(0);
  const [roles, setRoles] = useState({
    administrator: [],
    examinee: [],
    receptionist: [],
  });
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedRowCount, setSelectedRowCount] = useState(5);

  const toggleTab = (tab) => {
    if (activeItem !== tab) {
      setActiveItem(tab);
    }
  };

  useEffect(() => {
    const newRoles = {};

    tabItems.forEach((tabItem) => {
      newRoles[tabItem.value?.toLocaleLowerCase()] = persons.filter(
        (person) => person.role === tabItem.value?.toLocaleUpperCase()
      );
    });

    setRoles(newRoles);
  }, [persons]);

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }
  }, [dispatch, token]);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const { currentItems, totalPages, indexOfFirstItem } = handlePagination(
    roles[tabItems[activeItem].value?.toLowerCase()],
    currentPage,
    selectedRowCount
  );

  const handleChangeRowCount = (value) => {
    setCurrentPage(1);
    setSelectedRowCount(value);
  };

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text mx-3">
            {tabItems[activeItem].value?.toLocaleUpperCase()}S
          </a>
          <div className="d-flex">
            {tabItems.map((item, index) => (
              <MDBBtn
                key={`btn-${index}`}
                outline
                color="white"
                rounded
                size="sm"
                className={`px-2 ${classNames({
                  active: activeItem === index,
                })}`}
                onClick={() => toggleTab(index)}
              >
                <MDBIcon icon={item.icon} className="mt-0" />
              </MDBBtn>
            ))}
          </div>
        </MDBView>
        <MDBCardBody>
          <MDBTabContent activeItem={activeItem}>
            {tabItems.map((item, index) => (
              <MDBTabPane key={`tabpane-${index}`} tabId={index}>
                {roles[tabItems[activeItem].value?.toLowerCase()]?.length ===
                0 ? (
                  <div className="text-center">
                    No {tabItems[activeItem].value} Persons Found..
                  </div>
                ) : (
                  <MDBTable responsive hover className="text-center">
                    <MDBTableHead>
                      <tr>
                        <th>#</th>
                        <th className="th-lg">Username</th>
                        <th className="th-lg">Email</th>
                        <th className="th-lg">Date of Birth</th>
                        <th className="th-lg">Action</th>
                      </tr>
                    </MDBTableHead>

                    <MDBTableBody>
                      {currentItems.map((person, index) => (
                        <RowPerson
                          key={`tbody-${index + indexOfFirstItem}`}
                          person={person}
                          index={index + indexOfFirstItem}
                        />
                      ))}
                    </MDBTableBody>
                  </MDBTable>
                )}
                {roles[tabItems[activeItem].value?.toLowerCase()]?.length >
                  5 && (
                  <div className="ml-3">
                    <TableRowCount
                      handleChange={handleChangeRowCount}
                      selectedRowCount={selectedRowCount}
                    />
                  </div>
                )}

                {roles[tabItems[activeItem].value?.toLowerCase()]?.length >
                  selectedRowCount && (
                  <Pagination
                    totalPages={totalPages}
                    currentPage={currentPage}
                    onPageChange={handlePageChange}
                  />
                )}
              </MDBTabPane>
            ))}
          </MDBTabContent>
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
