import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import RowExam from "./row";
import { useDispatch, useSelector } from "react-redux";
import CreateExam from "./create";
import React, { useEffect, useState } from "react";

import { handlePagination } from "../../../../services/utilities";
import Pagination from "../../../../components/pagination";
import TableRowCount from "../../../../components/pagination/rows";
import { BROWSE } from "../../../../services/redux/slices/exams";

export default function Exams() {
  const { exams } = useSelector(({ exams }) => exams);
  const [modal, setModal] = useState(false);
  const { token } = useSelector(({ persons }) => persons);
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedRowCount, setSelectedRowCount] = useState(5);

  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }
  }, [dispatch, token]);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const { currentItems, totalPages, indexOfFirstItem } = handlePagination(
    exams,
    currentPage,
    selectedRowCount
  );

  const handleChangeRowCount = (value) => {
    setCurrentPage(1);
    setSelectedRowCount(value);
  };

  const toggle = () => {
    setModal(!modal);
  };

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 mx-4 mb-3 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text mx-3">
            Listed Exams
          </a>
          <div className="text-right">
            <MDBBtn
              outline
              color="white"
              rounded
              size="sm"
              className="px-2"
              onClick={toggle}
            >
              <MDBIcon icon="plus" className="mt-0" />
            </MDBBtn>

            <CreateExam modal={modal} toggle={toggle} />
          </div>
        </MDBView>
        <MDBCardBody>
          {exams.length === 0 ? (
            <div className="text-center">Exams Not Found..</div>
          ) : (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th>#</th>
                  <th className="th-lg">Title</th>
                  <th className="th-lg">Description</th>
                  <th className="th-lg">Action</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {currentItems.map((exam, index) => (
                  <RowExam
                    key={index + indexOfFirstItem}
                    index={index + indexOfFirstItem}
                    exam={exam}
                    toggle={toggle}
                  />
                ))}
              </MDBTableBody>
            </MDBTable>
          )}
          {exams.length > 5 && (
            <div className="ml-3">
              <TableRowCount
                handleChange={handleChangeRowCount}
                selectedRowCount={selectedRowCount}
              />
            </div>
          )}

          {exams.length > selectedRowCount && (
            <Pagination
              totalPages={totalPages}
              currentPage={currentPage}
              onPageChange={handlePageChange}
            />
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
