import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBTooltip,
  MDBView,
} from "mdbreact";
import RowSchedule from "./row";
import { useDispatch, useSelector } from "react-redux";
import CreateSchedule from "./create";
import { BROWSE } from "../../../../services/redux/slices/schedules";
import { handlePagination } from "../../../../services/utilities";
import TableRowCount from "../../../../components/pagination/rows";
import Pagination from "../../../../components/pagination";

export default function Schedules() {
  const { schedules } = useSelector(({ schedules }) => schedules);
  const [currentSchedules, setCurrentSchedules] = useState([]);
  const [modal, setModal] = useState(false);
  const { token } = useSelector(({ persons }) => persons);
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedRowCount, setSelectedRowCount] = useState(5);

  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }

    return () => {
      localStorage.removeItem("selectedSchedule");
    };
  }, [dispatch, token]);

  useEffect(() => {
    if (schedules) {
      setCurrentSchedules(schedules.filter((schedule) => !schedule.didFinish));
    }
  }, [schedules]);

  const toggle = () => {
    setModal(!modal);
  };

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const { currentItems, totalPages, indexOfFirstItem } = handlePagination(
    currentSchedules,
    currentPage,
    selectedRowCount
  );

  const handleChangeRowCount = (value) => {
    setCurrentPage(1);
    setSelectedRowCount(value);
  };

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 mx-4 mb-3 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text mx-3">
            Listed Schedules
          </a>
          <div className="text-right">
            <MDBTooltip placement="left">
              <MDBBtn
                outline
                color="white"
                rounded
                size="sm"
                className="px-2"
                onClick={toggle}
              >
                <MDBIcon icon="plus" className="mt-0" />
              </MDBBtn>
              <div>Add</div>
            </MDBTooltip>
            <CreateSchedule modal={modal} toggle={toggle} />
          </div>
        </MDBView>
        <MDBCardBody>
          {currentSchedules.length === 0 ? (
            <div className="text-center"> No Listed Schedules Yet..</div>
          ) : (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th>#</th>
                  <th className="th-lg">Title</th>
                  <th className="th-lg">Start</th>
                  <th className="th-lg">End</th>
                  <th className="th-lg">Exam</th>
                  <th className="th-lg">Proctor</th>
                  <th className="th-lg">Slots</th>
                  <th className="th-lg">Action</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {currentItems.map((schedule, index) => (
                  <RowSchedule
                    key={index + indexOfFirstItem}
                    index={index + indexOfFirstItem}
                    schedule={schedule}
                    toggle={toggle}
                  />
                ))}
              </MDBTableBody>
            </MDBTable>
          )}
          {currentSchedules.length > 5 && (
            <div className="ml-3">
              <TableRowCount
                handleChange={handleChangeRowCount}
                selectedRowCount={selectedRowCount}
              />
            </div>
          )}

          {currentSchedules.length > selectedRowCount && (
            <Pagination
              totalPages={totalPages}
              currentPage={currentPage}
              onPageChange={handlePageChange}
            />
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
