import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTabContent,
  MDBTabPane,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import classnames from "classnames";
import RowAppointment from "./row";
import find from "../../../../services/utilities/axioKit/find";

import { handlePagination } from "../../../../services/utilities"; // Import the handlePagination utility
import Pagination from "../../../../components/pagination";
import TableRowCount from "../../../../components/pagination/rows";

const tabItems = [
  { value: "Pending", icon: "clock" },
  { value: "Approved", icon: "thumbs-up" },
  { value: "Denied", icon: "thumbs-down" },
  { value: "Completed", icon: "check" },
  { value: "Failed", icon: "times" },
];

export default function Appointments() {
  const [examineeAppointments, setExamineeAppointments] = useState([]);
  const { token, auth } = useSelector(({ persons }) => persons);
  const [activeItem, setActiveItem] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedRowCount, setSelectedRowCount] = useState(5);

  const [statusData, setStatusData] = useState({
    pending: [],
    approved: [],
    denied: [],
    completed: [],
    failed: [],
  });

  const toggle = (tab) => {
    if (activeItem !== tab) {
      setActiveItem(tab);
    }
  };

  useEffect(() => {
    if (token && auth) {
      const handleAppointments = async () => {
        setExamineeAppointments(
          await find("appointments", "find", "examinee", auth._id, token)
        );
      };

      handleAppointments();
    }
  }, [token, auth]);

  useEffect(() => {
    const newStatusData = {};

    tabItems.forEach((tabItem) => {
      newStatusData[tabItem.value?.toLowerCase()] = examineeAppointments.filter(
        (appointment) => appointment.status === tabItem.value
      );
    });

    setStatusData(newStatusData);
  }, [examineeAppointments]);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const { currentItems, totalPages, indexOfFirstItem } = handlePagination(
    statusData[tabItems[activeItem].value?.toLowerCase()],
    currentPage,
    selectedRowCount
  );

  const handleChangeRowCount = (value) => {
    setCurrentPage(1);
    setSelectedRowCount(value);
  };

  return (
    <MDBContainer fluid>
      <MDBCard narrow>
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text">
            Status: {tabItems[activeItem].value}
          </a>

          <div className="d-flex">
            {tabItems.map((item, index) => (
              <MDBBtn
                key={`btn-${index}`}
                outline
                color="white"
                rounded
                size="sm"
                className={`px-2 ${classnames({
                  active: activeItem === index,
                })}`}
                onClick={() => toggle(index)}
              >
                <MDBIcon icon={item.icon} className="mt-0" />
              </MDBBtn>
            ))}
          </div>
        </MDBView>
        <MDBCardBody>
          <MDBTabContent activeItem={activeItem}>
            {tabItems.map((item, index) => (
              <MDBTabPane key={`tabpane-${index}`} tabId={index}>
                {statusData[item.value?.toLowerCase()]?.length === 0 ? (
                  <div className="text-center">
                    No {item.value} Appointments Yet..
                  </div>
                ) : (
                  <MDBTable responsive hover className="text-center">
                    <MDBTableHead>
                      <tr>
                        <th>#</th>
                        <th className="th-lg">Schedule</th>
                        <th className="th-lg">Start</th>
                        <th className="th-lg">End</th>
                        <th className="th-lg">Exam</th>
                        <th className="th-lg">Proctor</th>
                        {item.value === "Denied" && (
                          <>
                            <th className="th-lg">Reason</th>
                            <th className="th-lg">Action</th>
                          </>
                        )}
                        {item.value === "Approved" && <th>Action</th>}
                      </tr>
                    </MDBTableHead>

                    <MDBTableBody>
                      {currentItems.map((appointment, index) => (
                        <RowAppointment
                          key={`tbody-${index + indexOfFirstItem}`}
                          appointment={appointment}
                          index={index + indexOfFirstItem}
                          item={item}
                        />
                      ))}
                    </MDBTableBody>
                  </MDBTable>
                )}

                {statusData[tabItems[activeItem].value?.toLowerCase()]?.length >
                  5 && (
                  <div className="ml-3">
                    <TableRowCount
                      handleChange={handleChangeRowCount}
                      selectedRowCount={selectedRowCount}
                    />
                  </div>
                )}

                {statusData[tabItems[activeItem].value?.toLowerCase()]?.length >
                  selectedRowCount && (
                  <Pagination
                    totalPages={totalPages}
                    currentPage={currentPage}
                    onPageChange={handlePageChange}
                  />
                )}
              </MDBTabPane>
            ))}
          </MDBTabContent>
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
