import { MDBBtn, MDBIcon } from "mdbreact";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import { useHistory } from "react-router";
import { SELECTEDEXAM } from "../../../../services/redux/slices/banks";
import {
  DELETE,
  SELECTED,
  SETID,
} from "../../../../services/redux/slices/exams";
import { SETQUERY } from "../../../../services/redux/slices/scores";

export default function RowExam({ exam, toggle, index }) {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleDelete = () => {
    Swal.fire({
      icon: "question",
      title: "Are you sure?",
      text: "You want to delete this? You can't undo it.",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(SETID(exam._id));
        dispatch(DELETE({ id: exam._id, token }));
      }
    });
  };

  const handleUpdate = () => {
    toggle();
    dispatch(SELECTED(exam));
  };

  const handleBanks = () => {
    dispatch(SELECTEDEXAM(exam));
    history.push("/exams/banks");
  };

  const handleScores = () => {
    history.push("/scores");
    dispatch(SETQUERY({ key: "exam", value: exam }));
  };

  return (
    <tr>
      <th>{index + 1}.</th>
      <td>{exam.title}</td>
      <td>{exam.description}</td>
      <td>
        <div className="d-flex justify-content-center">
          <MDBBtn
            color="warning"
            outline
            rounded
            size="sm"
            className="px-2"
            onClick={handleUpdate}
          >
            <MDBIcon icon="pen" className="mt-0" />
          </MDBBtn>
          <MDBBtn
            color="danger"
            outline
            rounded
            size="sm"
            className="px-2"
            onClick={handleDelete}
          >
            <MDBIcon icon="times" className="mt-0" />
          </MDBBtn>
          <MDBBtn
            color="primary"
            outline
            rounded
            size="sm"
            className="px-2"
            onClick={handleBanks}
          >
            <MDBIcon icon="scroll" className="mt-0" />
          </MDBBtn>
          <MDBBtn
            color="success"
            outline
            rounded
            size="sm"
            className="px-2"
            onClick={handleScores}
          >
            <MDBIcon icon="medal" className="mt-0" />
          </MDBBtn>
        </div>
      </td>
    </tr>
  );
}
