import {
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import RowSchedule from "./row";
import find from "../../../../services/utilities/axioKit/find";
import { handlePagination } from "../../../../services/utilities";
import Pagination from "../../../../components/pagination";
import TableRowCount from "../../../../components/pagination/rows";

export default function Requests() {
  const { token, auth } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const [schedules, setSchedules] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedRowCount, setSelectedRowCount] = useState(5);

  useEffect(() => {
    if (token) {
      const handleSchedules = async () => {
        const _schedules = await find(
          "schedules",
          "find",
          "admin",
          auth._id,
          token
        );

        setSchedules(_schedules.filter((schedule) => !schedule.didFinish));
      };

      handleSchedules();
    }
  }, [dispatch, token, auth]);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const { currentItems, totalPages, indexOfFirstItem } = handlePagination(
    schedules,
    currentPage,
    selectedRowCount
  );

  const handleChangeRowCount = (value) => {
    setCurrentPage(1);
    setSelectedRowCount(value);
  };

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-3 mx-4 mb-3 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text mx-3">
            Schedules
          </a>
        </MDBView>
        <MDBCardBody>
          {schedules.length === 0 ? (
            <div className="text-center">No Schedules Yet..</div>
          ) : (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th>#</th>
                  <th className="th-lg">Schedule</th>
                  <th className="th-lg">Start</th>
                  <th className="th-lg">End</th>
                  <th className="th-lg">Exam</th>
                  <th className="th-lg">Slots</th>
                  <th className="th-lg">Action</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {currentItems.map((schedule, index) => (
                  <RowSchedule
                    key={index + indexOfFirstItem}
                    index={index + indexOfFirstItem}
                    schedule={schedule}
                  />
                ))}
              </MDBTableBody>
            </MDBTable>
          )}
          {schedules.length > 5 && (
            <div className="ml-3">
              <TableRowCount
                handleChange={handleChangeRowCount}
                selectedRowCount={selectedRowCount}
              />
            </div>
          )}

          {schedules.length > selectedRowCount && (
            <Pagination
              totalPages={totalPages}
              currentPage={currentPage}
              onPageChange={handlePageChange}
            />
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
