import { MDBBtn, MDBIcon } from "mdbreact";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  DELETE,
  PROMOTE_DEMOTE,
  SETID,
} from "../../../../services/redux/slices/persons";
import Swal from "sweetalert2";
import { useHistory } from "react-router";
import { SETQUERY } from "../../../../services/redux/slices/scores";
import { monthDayYearFormat } from "../../../../services/utilities/timeAndDateFormat";

export default function RowPerson({ person, index }) {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleDelete = () => {
    Swal.fire({
      icon: "question",
      title: "Are you sure?",
      text: "You want to delete this? You can't undo it.",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(SETID(person._id));
        dispatch(DELETE({ id: person._id, token }));
      }
    });
  };

  const handleScores = () => {
    history.push("/scores");
    dispatch(SETQUERY({ key: "examinee", value: person }));
  };

  const handlePromotion = () => {
    Swal.fire({
      icon: "question",
      title: "Are you sure?",
      text: `You want to ${
        person.role === "EXAMINEE"
          ? "promote this examinee to receptionist"
          : "demote this receptionist to examinee"
      }?`,
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(
          PROMOTE_DEMOTE({
            updatedRole: {
              _id: person._id,
              role: person.role === "EXAMINEE" ? "RECEPTIONIST" : "EXAMINEE",
            },
            token,
          })
        );

        Swal.fire({
          icon: "success",
          title: `Successfully ${
            person.role === "EXAMINEE"
              ? "promoted examinee to receptionist."
              : "demoted receptionist to examinee."
          }`,
          confirmButtonText: "Okay",
        }).then(() => {
          window.location.href = "/persons";
        });
      }
    });
  };

  return (
    <tr>
      <th>{index + 1}.</th>
      <td>{person.username}</td>
      <td>{person.email}</td>
      <td>{monthDayYearFormat(person.dob)}</td>
      <td>
        <div
          className={`${
            person.role === "EXAMINEE" ? "d-flex justify-content-center" : ""
          }`}
        >
          <MDBBtn
            color="danger"
            outline
            rounded
            size="sm"
            className="px-2"
            onClick={handleDelete}
          >
            <MDBIcon icon="times" className="mt-0" />
          </MDBBtn>
          {person.role !== "ADMINISTRATOR" && (
            <>
              {person.role !== "RECEPTIONIST" && (
                <MDBBtn
                  color="success"
                  outline
                  rounded
                  size="sm"
                  className="px-2"
                  onClick={handleScores}
                >
                  <MDBIcon icon="medal" className="mt-0" />
                </MDBBtn>
              )}

              <MDBBtn
                color={`${person.role === "EXAMINEE" ? "primary" : "info"}`}
                size="sm"
                onClick={handlePromotion}
                outline
                rounded
                className="px-2"
              >
                <MDBIcon
                  icon={`${
                    person.role === "EXAMINEE" ? "arrow-up" : "arrow-down"
                  }`}
                  className="mt-0"
                />
              </MDBBtn>
            </>
          )}
        </div>
      </td>
    </tr>
  );
}
