function handlePagination(items, currentPage, selectedRowCount) {
  const totalPages = Math.ceil(items.length / selectedRowCount);

  const indexOfLastItem = currentPage * selectedRowCount;
  const indexOfFirstItem = indexOfLastItem - selectedRowCount;

  const currentItems = items.slice(indexOfFirstItem, indexOfLastItem);

  return { currentItems, totalPages, indexOfFirstItem };
}

export default handlePagination;
