import { MDBBtn, MDBIcon } from "mdbreact";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  DELETE,
  SELECTED,
  SETID,
} from "../../../../services/redux/slices/banks";
import Swal from "sweetalert2";

export default function RowBank({ bank, toggle, index }) {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();

  const handleDelete = () => {
    Swal.fire({
      icon: "question",
      title: "Are you sure?",
      text: "You want to delete this? You can't undo it.",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(SETID(bank._id));
        dispatch(DELETE({ id: bank._id, token }));
        window.location.href = "/exams/banks";
      }
    });
  };

  const showChoices = () => {
    return (
      <div>
        {bank.choices.map((choice, index) => (
          <li key={index} className="d-flex align-items-center ">
            {`${String.fromCharCode(65 + index)}. ${choice}`}
          </li>
        ))}
      </div>
    );
  };

  const handleUpdate = () => {
    toggle();

    dispatch(SELECTED(bank));
  };

  return (
    <tr>
      <th>{index + 1}.</th>
      <td>{bank.question}</td>
      <td>{showChoices()}</td>
      <td>{`${String.fromCharCode(65 + bank.answer)}. ${
        bank.choices[bank.answer]
      }`}</td>
      <td>
        <MDBBtn
          color="warning"
          outline
          rounded
          size="sm"
          className="px-2"
          onClick={handleUpdate}
        >
          <MDBIcon icon="pen" className="mt-0" />
        </MDBBtn>
        <MDBBtn
          color="danger"
          outline
          rounded
          size="sm"
          className="px-2"
          onClick={handleDelete}
        >
          <MDBIcon icon="times" className="mt-0" />
        </MDBBtn>
      </td>
    </tr>
  );
}
